.cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  
  .card {
    margin: 10px;
    padding: 5px;
    width: 300px;
    min-height: 100px;
    display: grid;
  
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.1s;
  }
  
  .card:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    transform: scale(1.01);
  }
  
  .card__link,
  .card__exit,
  .card__icon {
    position: relative;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.9);
  }
  
  .card__link::after {
    position: absolute;
    top: 25px;
    left: 0;
    content: "";
    width: 0%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.6);
    transition: all 0.5s;
  }
  
  .card__link:hover::after {
    width: 100%;
  }
  
  .card__exit {
    grid-row: 1/2;
    justify-self: end;
  }
  
  .card__icon {
    grid-row: 2/3;
    font-size: 30px;
  }
  
  .card__title {
    /* grid-row: 3/4; */
    font-weight: 400;
    color: #ffffff;
  }
  
  .card__apply {
    /* grid-row: 4/5; */
    margin-top: 15px;
    align-self: center;
  }
  
  /* CARD BACKGROUNDS */
  
  .card-1 {
    background: radial-gradient(#1fe4f5, #3fbafe);
  }
  
  /* RESPONSIVE */
  
  .dyno0 {
    background: radial-gradient(#1fe4f5, #3fbafe);
  }
  
  .dyno1 {
    background: radial-gradient(#fbc1cc, #fa99b2);
  }
  
  .dyno2 {
    background: radial-gradient(#76b2fe, #b69efe);
  }
  
  .dyno3 {
    background: radial-gradient(#60efbc, #58d5c9);
  }
  
  .dyno {
    background: radial-gradient(#f588d8, #c0a3e5);
  }
  

  @media (max-width: 1600px) {
    .cards {
      justify-content: center;
    }
  }
  