



*{
  box-sizing: border-box;  
  padding: 0;
  margin: 0;
  font-family: "Josefin", "Arial Narrow";
}

.header{
    width: 100%;
    line-height: 100px;
    background-color: #f5ba13;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 50px;
    color: white;
    box-shadow: 5px 3px 15px -5px black;
}

.main_note{
    width: auto;
    max-width: 500px;
    padding:15px 10px 5px 10px;
    margin:20px auto;
   
}
.formnote{
    background: radial-gradient(#fbc1cc, #fa99b2);
}
 
 
.my_notes h1{
    color: #34495e;
}

.my_notes p{
    color: grey;
}

form{
    min-height: 50px;
    color: #ecf0f1;
    padding: 15px 10px 18px 10px;
    box-shadow: 3px 5px 15px -5px black;
    border-radius: 10px;
    position: relative;
}

input{
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    /* font-size: 1.5rem; */
    font-weight: bolder;
    color:#34495e

}

textarea{
    width: 100%;
    line-height: 30px;
    background:transparent;
    border: none;
    outline: none;
    font-size: 1rem;
    margin: 10px 0;
   
    font-weight:normal;
}

.MuiButton-root{
    width: 30px;
    height: 50px;
    position: absolute;
    bottom: -28px;
    left: 400px;
    cursor: pointer;
    background-color: #fff !important  ;
    box-shadow: 5px 5px 15px -5px black;
    border-radius: 50% !important;
}

.MuiButton-root .plus_sign{
    font-size: 45px !important;
    font-size: 900;
    color: #f4b400;
    cursor: pointer;
    z-index: 222;
    background:transparent;
}

.MuiButton-root:hover .plus_sign{
    color: green;
    background: transparent;
}

.note {
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
    position: relative;
    justify-content: space-between;
    flex-direction: column;
    display: flex;
    
}
.content{
    max-height:100px;
    overflow-y: scroll;
    overflow-x: hidden;  
   
}

.note h1{
    font-size: 1.5rem;
    margin-bottom: 3px;
    color: #34495e;
}

.note p{
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
    color: grey;

}

  .note .btn-holder {
    justify-content: flex-end;
    display: flex;
  }



.deleteIcon{
    width: 0.7em;
    height: 0.7em;
    position: absolute;
    top: 1px;
    right: 400px;
    color: black;
    background-color: black;
}




.note .btn:hover.deleteIcon{
    color: #fff;
}
.deleteIcon:hover{
    color: red;
}

footer{
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
}
input[type="file"] {
    display: none;
}

* {
    scrollbar-width: thin;
    scrollbar-color: #f1f1f1 #888;
}
*::-webkit-scrollbar {
    width: 5px;
}
  
  /* Track */
*::-webkit-scrollbar-track {
    background: #f1f1f1;
}
  
  /* Handle */
*::-webkit-scrollbar-thumb {
    background: #888;
}
  
  /* Handle on hover */
*::-webkit-scrollbar-thumb:hover {
    background: #555;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: black;
    opacity: 0.8; /* Firefox */
  }