.cards {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
  
  .card {
    margin: 10px;
    padding: 5px;
    width: 300px;
    min-height: 100px;
    display: grid;
  
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.1s;
  }
  
  .card:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }
  
  .card__link,
  .card__exit,
  .card__icon {
    position: relative;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.9);
  }
  
  .card__link::after {
    position: absolute;
    top: 25px;
    left: 0;
    content: "";
    width: 0%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.6);
    transition: all 0.5s;
  }
  
  .card__link:hover::after {
    width: 100%;
  }
  
  .card__exit {
    grid-row: 1/2;
    justify-self: end;
  }
  
  .card__icon {
    grid-row: 2/3;
    font-size: 30px;
  }
  
  .card__title {
    /* grid-row: 3/4; */
    font-weight: 400;
    color: #ffffff;
  }
  
  .card__apply {
    /* grid-row: 4/5; */
    margin-top: 15px;
    -webkit-align-self: center;
            align-self: center;
  }
  
  /* CARD BACKGROUNDS */
  
  .card-1 {
    background: radial-gradient(#1fe4f5, #3fbafe);
  }
  
  /* RESPONSIVE */
  
  .dyno0 {
    background: radial-gradient(#1fe4f5, #3fbafe);
  }
  
  .dyno1 {
    background: radial-gradient(#fbc1cc, #fa99b2);
  }
  
  .dyno2 {
    background: radial-gradient(#76b2fe, #b69efe);
  }
  
  .dyno3 {
    background: radial-gradient(#60efbc, #58d5c9);
  }
  
  .dyno {
    background: radial-gradient(#f588d8, #c0a3e5);
  }
  

  @media (max-width: 1600px) {
    .cards {
      -webkit-justify-content: center;
              justify-content: center;
    }
  }
  

  .our-team {
    padding:  30px;
    margin-bottom: 30px;
    background: radial-gradient(#76b2fe, #b69efe);
    text-align: center;
    overflow: hidden;
    position: relative;
  }
  
  .our-team .picture {
    display: inline-block;
    height: 130px;
    width: 130px;
    margin-bottom: 30px;
    z-index: 1;
    position: relative;
  }
  
  .our-team .picture::before {
    content: "";
    width: 100%;
    height: 0;
    border-radius: 50%;
    background: radial-gradient(#76b2fe, #b69efe);
    position: absolute;
    bottom: 135%;
    right: 0;
    left: 0;
    opacity: 0.9;
    -webkit-transform: scale(3);
            transform: scale(3);
    transition: all 0.3s linear 0s;
  }
  
  .our-team:hover .picture::before {
    height: 100%;
  }
  
  .our-team .picture::after {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: radial-gradient(#76b2fe, #b69efe);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  
  .our-team .picture img {
    width: 100%;
    height: auto;
    border-radius: 50%;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all 0.9s ease 0s;
  }
  
  .our-team:hover .picture img {
    box-shadow: radial-gradient(#76b2fe, #b69efe);
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }
  
  .our-team .title {
    display: block;
    font-size: 20px;
    color: #4e5052;
    font-weight: bold;
   
  }
  
  .our-team .social {
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: #1369ce;
    position: absolute;
    bottom: -100px;
    left: 0;
    transition: all 0.5s ease 0s;
  }
  
  .our-team:hover .social {
    bottom: 0;
  }
  
  .our-team .social li {
    display: inline-block;
  }
  
  .our-team .social li a {
    display: block;
    padding: 10px;
    font-size: 17px;
    color: white;
    transition: all 0.3s ease 0s;
    text-decoration: none;
  }
  
  .our-team .social li a:hover {
    color: #1369ce;
    background-color: #f7f5ec;
  }
  



  

  
  .card-container {
